.header-editor {
	// height: 3.75rem;
	background: $neutralShade7 url("data:image/png;base64,") 0;

	.navbar {
		display: flex;
		align-items: center;
	}

	.navbar-brand {
		width: 3.125rem;

		display: inline-block;

		margin-right: 1rem;

		img {
			display: block;
		}
	}

	.navbar-nav {
		padding-left: 1rem;

		display: flex;

		box-shadow: inset 1px 0 0 rgba(255, 255, 255, 0.1);
	}

	.nav-item {
		color: rgba(255, 255, 255, 0.6);
		transition: color 0.3s ease-in-out;

		display: flex;
		flex-direction: column;
		align-items: center;
		
		font-size: 0.7rem;
		text-transform: uppercase;
		text-decoration: none;

		padding: 1rem 0.8rem;

		i {
			font-size: 1.5rem;
		}

		&:hover {
			color: $light;
		}
	}
}
